.usersPage{
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    font-size: large;
    justify-content: space-evenly;
    background-color: rgb(202, 232, 222);
}
.loggedinas{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: larger;
    color: rgb(6, 6, 166);
} 
.userCard{
    /* max-height: 35vh; */
    height: fit-content;
    width: 25%;
    min-width: 350px;
    background-color: rgb(145, 135, 222);
    padding: 1rem;
    margin: 1rem;
    border-radius: 10%;
    box-shadow: 2px 2px 2px 2px rgb(118, 17, 212);
    color: rgb(249, 240, 255);
    font-size: larger;
}
.userDetail{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
}