.userDetailPage{
    display: flex;
    /* flex-direction: column; */
    background-color: rgb(216, 177, 250);
    min-height: 100vh;
    justify-content: center;
}
.detailsList{
    display: flex;
    max-width: 50%;
    flex-wrap: wrap;
    font-size: large;
    text-align: center;
    padding: 1rem;
    min-width: 350px;
    font-family: cursive;
    font-size: 1.5rem
}
.updateForm{
    display: flex;
    max-width: 50%;
    min-width: 340px;
    flex-wrap: wrap;
    text-align: center;
}
label,input,select{
    font-family: cursive;
    font-size: 1.5rem;
    width: 90%;
    text-align: center;
}
button{
    font-family: cursive;
    font-size: 1.5rem;
}

@media (max-width: 400px) {
    label,input,select{
        font-family: cursive;
        font-size: 1.5rem;
        width: 90%;
    }
    button{
        font-family: cursive;
        font-size: 1.5rem;
    }
}